
/* HQTheme Styles */

#main-wrapper{
	.container-fixed();
}

.clear{
	clear: both;
}

button{
	//color: @background-color;
}

h1, .h1, h2, .h2, h3, .h3{
	margin: 10px 0;
}

blockquote {
	border: none;
	border-bottom: 1px solid #fff;
	min-height: 70px;
	position: relative;
	border: 0 none;
	&:before {    
		font-family: 'FontAwesome';
		position: absolute;
		content: '\f10e';
		font-size: 66px;
		line-height: 66px;
		left: 5px;
	}
	p{
		margin-left: 70px;
	}
}

table{
	width: 100%;
	th{
		padding: 5px;
	}
	td{
		border: 1px solid #fff;
		padding: 5px;
	}
}

dl dd{
	margin-left: 1em;
}

address{
	font-style: italic;
}

q{
	font-style: italic;
}
cite{
	font-style: italic;
}

ins{
	text-decoration: none;
	padding: 2px;
}

.twitter-tweet{
	width: 100%!important;
}

.footer_bottom,
.header_topline{
	font-size: 12px;
	.social{
		font-size: 18px;
		margin-right: 10px;
	}
	.socials,
	.search,
	.socials-search{
		padding-top: 8px;
	}
	li{
		margin: 0 5px;
	}
}
.header_topline{
	padding-bottom: 5px;
	margin-bottom: 3px;
	.text{
		padding-top: 8px;
	}
}
.footer_bottom{
	padding-top: 5px;
	margin-top: 3px;
	.text{
		padding-bottom: 8px;
	}
}

/* Top Menus */
.sm-header_topline {
	a{
		font-size: 12px;
	}
	li{
		padding-top: 6px;
		border-top: 3px solid transparent;
		a{
			text-transform: uppercase;
		}
	}
}
/* END Top Menus */
/* Footer Menus */
.sm-footer_bottom {
	a{
		font-size: 12px;
	}
	li{
		padding-top: 4px;
		border-bottom: 3px solid transparent;
		a{
			text-transform: uppercase;
		}
	}
}
/* END Footer Menus */

/* Search form */
.sb-search{
	top: 3px;
}

.sb-search-out{
	width: 12px;
	height: 12px;
	display: inline-block;
}
.sb-icon-search{
	font-size: 18px;
}
.sb-search-input{
	font-size: 14px;
    height: 28px;
}
.sb-icon-search, 
.sb-search-submit{
	height: 28px;
    line-height: 28px;
}
/* END Search form */

/* Main Navidation */

.navbar{
	display: inline-block;
	margin: 0;
	.navbar-toggle{
		margin: 20px 0 0 0;
	}
}

/* Breadcrumps */
#breadcrumbs{
    list-style: none;
    margin: 10px 0;
    overflow: hidden;
	li{
		float: left;
		margin-right:15px;
		&.separator{
			padding-top: 3px;
		}
	}
}
/* END Breadcrumps */


/* Comments */
.comment-list{
	padding: 0;
	.comment {
		margin-top: 1px;
		.comment-body {
			display: table-row;
			width: 100%;
			.time{
				display: block;
				font-size: 10px;
			}
			.comment-info{
				padding: 10px;
				display: table-cell;
				width: 30%;
				img{
					float: left;
					margin: 0 5px 5px 0;
					
					-webkit-border-radius: 50%;
					-moz-border-radius: 50%;
					border-radius: 50%;
				}
			}
			.comment-content{
				padding: 10px;
				display: table-cell;
				width: 70%;
				font-size: 12px;
			}
			.comment-reply-link{
				.hqbtn;
				//.btn-primary;
				clear: both;
				margin-top: 5px;
				&:before {
					.fa;
					content: '\f122';
					margin-right: 10px;
				}
			}
			.comment-edit-link{
				.hqbtn;
				clear: both;
				margin-top: 5px;
				&:before {
					.fa;
					content: '\f044';
					margin-right: 10px;
				}
			}
		}
	}
	.parent{
		.comment{
			width: 90%;
			margin-left: 10%;
		}
	}
}
#commentform {
	.form-submit input{
		.hqbtn;
		//.btn-primary;
		.btn-lg;
		text-transform: uppercase;
	}
	textarea{
		.form-control;
		height: 80px;
	}
	input[type="text"] {
		.form-control;
		display: inline-block;
		.make-sm-column(3.8);
		margin-bottom: 20px;
		padding: 10px;
	}
	#email{
		margin-left: 20px;
	}
	#url{
		float: right;
	}
}
.comment-reply-title{
	text-transform: uppercase;
	padding: 20px 0 10px 0;
	margin-bottom: 15px;
	#cancel-comment-reply-link{
		.hqbtn;
		.btn-xs;
		float: right;
	}
}
/* END Comments */

.hqbtn {
  display: inline-block;
  margin-bottom: 0; // For input.btn
  font-weight: @btn-font-weight;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
  border: 1px solid transparent;
  white-space: nowrap;
  .button-size(@padding-base-vertical; @padding-base-horizontal; @font-size-base; @line-height-base; @border-radius-base);
  .user-select(none);

  &,
  &:active,
  &.active {
    &:focus {
      .tab-focus();
    }
  }

  &:hover,
  &:focus {
    color: @btn-default-color;
    text-decoration: none;
  }

  &:active,
  &.active {
    outline: 0;
    background-image: none;
    .box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: not-allowed;
    pointer-events: none; // Future-proof disabling of clicks
    .opacity(.65);
    .box-shadow(none);
  }
}